body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.img-eighty-percent {
  max-width: 80%;
  max-height: 80%;
}

.technology-icon-container {
  width: 40%;
  height: 40%;
  max-width: 100px;
  max-height: 100px;

  margin-bottom: 24px;
}

.form-item {
  margin-bottom: 0px;
  padding-top: 8px !important;
}

.input-group {
  padding: 12px;
}

.ant-collapse-content {
  padding-left: 26px;
}
body { font-family: Avenir; }

.App {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

.container {
  margin-left: auto;
  margin-right: auto;
}

h1 { margin-bottom: 16px; }
h2 { margin-bottom: 12px; }
h3, h4, h5, h6 { margin-bottom: 8px; }

.brand-title {
  color: white;
  font-family: 'Playfair Display', 'Merriweather', 'Avenir', serif;
}

.center {
  text-align: center;
}

.text-right { text-align: right; }

.l-high-padding-top {
  padding-top: 48px;
}

.l-spacing-heigh {
  padding: 48px 0 !important;
}

.is-centered {
  margin-left: auto;
  margin-right: auto;
}

.text-bold { font-weight: bold; }
.text-primary { color: #1890ff; }

.is-clickable { cursor: pointer; }

.opacity-lighter { opacity: 0.6; }

.is-clickable>.opacity-lighter:hover { opacity: 0.73; }

.ant-row { padding-top: 24px; }
.ant-layout { background-color: white; }

@media (min-width: 1281px) {
  .container { width: 1080px; }  
}

@media (min-width: 1025px) and (max-width: 1280px) { 
  .container { width: 960px; }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .container { width: 90%; }
}

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .container { width: 90%; }
}

@media (min-width: 481px) and (max-width: 767px) {}

@media (min-width: 320px) and (max-width: 480px) {}
